/* usage : 

    @media VARIABLE_NAME {
        // style in here 
    }
*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  background-color: #effafa;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

header {
  width: 100%;
  height: 9.75rem;
  background-color: #5ba4a4;
  background-image: url(../../images/bg-header-desktop.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
@media only screen and (max-width: 759px) {
  header {
    background-image: url(../../images/bg-header-mobile.svg);
  }
}

main {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  padding: 0rem 1.5rem;
  background-color: transparent;
  width: 100%;
  max-width: 69.375rem;
}
main .wrapper {
  position: relative;
}
main .wrapper .filterBar {
  display: flex;
  flex-flow: row nowrap;
  width: 100%;
  height: auto;
  background-color: #ffffff;
  box-shadow: 0px 2px 30px -10px #5ba4a4;
  border-radius: 0.4rem;
  padding: 1.25rem 1.75rem;
  position: absolute;
  transform: translateY(-2.25rem);
}
@media only screen and (max-width: 759px) {
  main .wrapper .filterBar {
    position: relative;
    margin-bottom: -2rem;
    padding-bottom: 0.25rem;
  }
}
main .wrapper .filterBar .FTWrapper {
  display: flex;
  flex-flow: row wrap;
}
main .wrapper .filterBar .FTWrapper .filterTags {
  background-color: #eef6f6;
  height: 2rem;
  border-radius: 0.25rem;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  overflow: hidden;
  margin-right: 1rem;
}
@media only screen and (max-width: 759px) {
  main .wrapper .filterBar .FTWrapper .filterTags {
    margin: 0rem 1rem 1rem 0rem;
  }
}
main .wrapper .filterBar .FTWrapper .filterTags .tag {
  color: #5ba4a4;
  font-size: 0.9rem;
  font-weight: 700;
  margin-left: 0.5rem;
}
main .wrapper .filterBar .FTWrapper .filterTags .deselect {
  background-color: #5ba4a4;
  background-image: url(../../images/icon-remove.svg);
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  width: 2rem;
  height: 2rem;
  margin-left: 0.5rem;
  cursor: pointer;
}
main .wrapper .filterBar .FTWrapper .filterTags .deselect:active {
  background-color: #2c3a3a;
}
main .wrapper .filterBar .clear {
  background-color: transparent;
  border: none;
  margin-left: auto;
  color: #5ba4a4;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition-duration: 0.5s;
  text-decoration-color: transparent;
}
main .wrapper .filterBar .clear:hover {
  text-decoration: underline;
  text-decoration-thickness: 0.1rem;
}
main .jobList {
  height: fit-content;
  width: 100%;
  margin-top: 4.6875rem;
}
@media only screen and (max-width: 759px) {
  main .jobList {
    margin-top: 3rem;
  }
}
main .jobList .jobCard {
  display: flex;
  flex-flow: row nowrap;
  height: 9.5rem;
  width: 100%;
  background-color: #ffffff;
  overflow: hidden;
  border-radius: 0.4rem;
  margin-bottom: 20px;
  box-shadow: 0 20px 30px -30px #5ba4a4;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard {
    height: fit-content;
    margin-bottom: 2.5rem;
  }
}
main .jobList .jobCard::before {
  content: "";
  display: block;
  width: 5px;
  height: 100%;
  background-color: #5ba4a4;
  position: relative;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard::before {
    height: auto;
  }
}
main .jobList .jobCard .jobContentWrapper {
  display: flex;
  flex-flow: row nowrap;
  padding: 2rem 2.2rem;
  align-items: center;
  width: 100%;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper {
    flex-flow: column nowrap;
    align-items: unset;
    padding: 2rem 1rem;
  }
}
main .jobList .jobCard .jobContentWrapper img {
  height: 5.5rem;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper img {
    height: 3rem;
    position: absolute;
    transform: translateY(-3.5rem);
  }
}
main .jobList .jobCard .jobContentWrapper .info {
  display: flex;
  flex-flow: column nowrap;
  margin-left: 1.5625rem;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .info {
    margin-left: unset;
  }
}
main .jobList .jobCard .jobContentWrapper .info .company {
  display: flex;
  flex-direction: row;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .info .company * {
    margin-top: 0.2rem;
  }
}
main .jobList .jobCard .jobContentWrapper .info .company .companyName {
  color: #5ba4a4;
  margin-right: 1rem;
  font-weight: 700;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .info .company .companyName {
    margin-top: 0.4rem;
  }
}
main .jobList .jobCard .jobContentWrapper .info .company .new {
  background-color: #5ba4a4;
  color: #ffffff;
  border-radius: 1rem;
  font-size: 0.8rem;
  padding: 0.3rem 0.5rem 0rem;
  margin-right: 0.5rem;
}
main .jobList .jobCard .jobContentWrapper .info .company .featured {
  background-color: #2c3a3a;
  color: #ffffff;
  border-radius: 1rem;
  font-size: 0.8rem;
  padding: 0.3rem 0.5rem 0rem;
  margin-right: 0.5rem;
}
main .jobList .jobCard .jobContentWrapper .info h1 {
  font-size: 1.25rem;
  font-weight: 700;
  color: #2c3a3a;
  transition-duration: 0.15s;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .info h1 {
    font-size: 0.8rem;
  }
}
main .jobList .jobCard .jobContentWrapper .info h1:hover {
  color: #5ba4a4;
  cursor: pointer;
}
main .jobList .jobCard .jobContentWrapper .info .jobAttr {
  color: #7b8e8e;
  margin: 0.8rem 0rem;
  user-select: none;
}
main .jobList .jobCard .jobContentWrapper .attributes {
  margin-left: auto;
  display: flex;
  flex-flow: row wrap;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .attributes {
    border-top: 1px solid #7b8e8e;
    margin-left: unset;
  }
}
main .jobList .jobCard .jobContentWrapper .attributes .attrTag {
  background-color: #eef6f6;
  color: #5ba4a4;
  border-radius: 0.2rem;
  font-size: 1rem;
  font-weight: 700;
  padding: 0.5rem 0.5rem 0.3rem;
  margin-left: 1rem;
  transition-duration: 0.15s;
}
@media only screen and (max-width: 759px) {
  main .jobList .jobCard .jobContentWrapper .attributes .attrTag {
    margin: 1rem 1rem 0rem 0rem;
  }
}
main .jobList .jobCard .jobContentWrapper .attributes .attrTag:hover {
  background-color: #5ba4a4;
  color: #ffffff;
  cursor: pointer;
}

* {
  font-family: "League Spartan", sans-serif;
}

h1, h2, h3 {
  margin-top: 0px;
}

h1 {
  text-transform: uppercase;
  color: #000000;
  margin: 0;
}

body {
  font-size: 0.9375rem;
}